import React from 'react'
import styled from 'styled-components'
import tw from 'tailwind.macro'
import { Parallax } from 'react-spring/renderprops-addons.cjs'

// Components
import Layout from '../components/Layout'
import ProjectCard from '../components/ProjectCard'

// Elements
import { Title, AltTitle, BigTitle, Subtitle } from '../elements/Titles'

// Views
import Hero from '../views/Hero'
import Projects from '../views/Projects'
import About from '../views/About'

import avatar from '../images/avatar.jpg'

const ProjectsWrapper = styled.div`
  ${tw`flex flex-wrap justify-between mt-8`};
  display: grid;
  grid-gap: 4rem;
  grid-template-columns: repeat(2, 1fr);
  @media (max-width: 1200px) {
    grid-gap: 3rem;
  }
  @media (max-width: 900px) {
    grid-template-columns: 1fr;
    grid-gap: 2rem;
  }
`

const AboutHero = styled.div`
  ${tw`flex flex-col lg:flex-row items-center justify-center mt-8`};
`

const Avatar = styled.img`
  ${tw`rounded-full w-32 xl:w-48 shadow-lg h-auto`};
`

const AboutSub = styled.span`
  ${tw`text-white text-lg md:text-xl lg:text-2xl font-sans pt-12 lg:pt-0 lg:pl-12`};
`

const AboutDesc = styled.p`
  ${tw`text-white text-lg md:text-xl lg:text-2xl font-sans pt-6 md:pt-12 text-center`};
`

const ToolBoxWrapper = styled.ul`
  list-style: none;
  margin: 2em 0 0;
  padding: 0;
  overflow: hidden;
`

const ToolBoxItem = styled.li`
  ${tw`font-sans`};
  margin: .4em .8em .4em 0;
  padding: .45em .8em .4em;
  display: inline-block;
  background: #fff;  
`

const ToolBoxHeader = styled.p`
  ${tw`text-white text-lg md:text-xl lg:text-2xl font-sans pt-6 md:pt-12 text-center`};
`

const Footer = styled.footer`
  ${tw`text-center text-white pin-b p-6 font-sans text-md lg:text-lg`};
`

const Index = () => (
  <>
    <Layout />
    <Parallax pages={4.1}>
      <Hero offset={0}>
        <BigTitle>
          Hi, <br /> I'm Vishwas Shetty.
        </BigTitle>
        <Subtitle>I'm an aspiring Full Stack Developer.<br/> I pride myself on crafting experiences that bridges the gap between what’s desirable (by people) and what’s possible (using technology).</Subtitle>
      </Hero>
      <Projects offset={1}>
        <AltTitle>WORK</AltTitle>
        <ProjectsWrapper>
          <ProjectCard
            title="playoff predictor"
            link=""
            bg="radial-gradient(circle at -4% -12.9%, rgb(74, 98, 110) 0.3%, rgb(30, 33, 48) 90.2%)"
          >
            Web application that allows users to predict nba playoff games
          </ProjectCard>
          <ProjectCard
            title="The Reflector"
            link=""
            bg="linear-gradient(108.3deg, rgb(202, 73, 118) 15.2%, rgb(255, 84, 84) 99.3%)"
          >
            Interactive installation to understand role of Social Media in people's lives
          </ProjectCard>
          <ProjectCard
            title="It Depends"
            link=""
            bg="linear-gradient(107deg, rgb(13, 198, 180) 8.1%, rgb(33, 198, 138) 79.5%)"
          >
            Phrase Tracker
          </ProjectCard>
          <ProjectCard
            title="Checkmate"
            link=""
            bg="linear-gradient(90.9deg, rgb(3, 195, 195) 0.3%, rgb(37, 84, 112) 87.8%)"
          >
            Automated bagging self-checkout system to alleviate bagging issues
          </ProjectCard>
        </ProjectsWrapper>
      </Projects>
      <About offset={3}>
        <Title>ABOUT</Title>
        <AboutHero>
          <Avatar src={avatar} alt="John Doe" />
          <AboutSub><a href="http://vishwasshetty.com/VishwasShetty_resume.pdf">Resume</a></AboutSub>
        </AboutHero>
        <ToolBoxWrapper>
          <ToolBoxHeader>My Toolbox:</ToolBoxHeader>
          <ToolBoxItem>HTML5</ToolBoxItem>
          <ToolBoxItem>CSS/CSS3</ToolBoxItem>
          <ToolBoxItem>SCSS/SASS</ToolBoxItem>
          <ToolBoxItem>JavaScript</ToolBoxItem>
          <ToolBoxItem>Babel</ToolBoxItem>
          <ToolBoxItem>Git</ToolBoxItem>
          <ToolBoxItem>React</ToolBoxItem>
          <ToolBoxItem>Node.js</ToolBoxItem>
          <ToolBoxItem>Cognito</ToolBoxItem>
          <ToolBoxItem>IAM</ToolBoxItem>
          <ToolBoxItem>API Gateway</ToolBoxItem>
          <ToolBoxItem>S3</ToolBoxItem>
          <ToolBoxItem>DynamoDB</ToolBoxItem>
          <ToolBoxItem>Lambda</ToolBoxItem>
        </ToolBoxWrapper>
        <AboutDesc>
          Current Gig: UX Developer @ <a href="https://knackshops.com">Knackshops.com</a><br/><br/>
          I love people.<br/>
          I adore nature.<br/>
          I breathe sports.<br/>
          I live for experiences.<br/>
          I believe in global warming.
        </AboutDesc>
        <Footer>
          &copy; 2019 Carved and Chiseled from <a href="https://www.gatsbyjs.org/">Gatsby</a> and <a href="https://reactjs.org/">React</a>
        </Footer>
      </About>
    </Parallax>
  </>
)

export default Index
